// @flow
import { observable } from 'mobx'
import Victor from "victor"
import guid from "./guid"
import Unit from "./Unit"
import type { Coordinate } from "./GameTypes"
import { gameParams } from "./gameInfo"
import { metersPerSecondToPixelsPerTick } from "./TickUtils"

class Bullet {
    id: number
    willHit: boolean
    sourceUnit: Unit
    targetUnit: Unit
    sourceCoordinate: Coordinate
    currentCoordinate: Coordinate
    displayOffset: Coordinate
    velocity: Coordinate
    accuracy: number

    @observable currentCoordinate: Coordinate;

    constructor({ sourceUnit, targetUnit }:
        {
            sourceUnit: Unit, targetUnit: Unit
        }) {

        const diff = new Victor(targetUnit.x - sourceUnit.x, targetUnit.y - sourceUnit.y).normalize();
        let bulletVelocityPixelsPerTick = metersPerSecondToPixelsPerTick(gameParams.bulletVelocity)

        this.id = guid()
        this.sourceUnit = sourceUnit
        this.targetUnit = targetUnit
        this.accuracy = sourceUnit.accuracy(targetUnit)
        this.sourceCoordinate = { x: sourceUnit.x, y: sourceUnit.y }
        this.currentCoordinate = { x: sourceUnit.x, y: sourceUnit.y }
        this.velocity = { x: diff.x * bulletVelocityPixelsPerTick, y: diff.y * bulletVelocityPixelsPerTick}
        this.willHit = true
        this.displayOffset = { x: parseInt(Math.random() * 20) - 10, y: parseInt(Math.random() * 20) - 10 }
    }
}

export default Bullet



